/**
 * 
 * @param {any} customId
 * @param {String} phone
 */
export default function sendSMS(customId, phone) { // , 
	let code = ''; // 
	switch (customId) { // 
		case '1':
			code = '';
			break;
		default: //
			code = '123456';
			break;
	}
	//
	uni.showToast({title: `${phone},`, icon: 'none'});
	return code; // return
}