import log from './config/log';
import { getSendData, getAsyncDataFc } from './config/picker';
import interfaces from './config/interfaces';
import { inputCustomTapFc, inputCustomTapCatchFc, filterTypeObj, verifyTypeObj } from './config/input';
import { UpLoadFile, pics_splice } from './config/pics';
import sendSMS from './config/sms';

const getPickerAsyncData = async function(obj) { // picker-custompicker-custom2
	let _this = this;
	_this.waiting = true; // 
	const {
		value, // pickervalue
		index, // picker
		launch, // 
		datas, // picker
		oldValue, // pickervalue
		customId, // 
		linkageNum // 
	} = obj; // pickervalue  : {data_0: , data_1: , data_2: }, pickervalue, 
	const {
		data_0, // 
		data_1 // 
	} = datas; // : {data_0: , data_1: , data_2: }

	// sendData, async_1async_2
	let sendData = {};
	try {
		switch (linkageNum) {
			case 2: // 
				if (!!_this.memoryData_1[value[0]]) { // 
					_this.async_1 = [..._this.memoryData_1[value[0]]]; // 
				} else {
					sendData = getSendData(obj); // 
					const newData_1 = await getAsyncDataFc(sendData); // 
					_this.async_1 = [...newData_1]; // 
					_this.memoryData_1[value[0]] = [...newData_1]; // 
				}
				if (launch) { //  valuedefaultValue
					_this.value[1] = value[1]; // 
				} else {
					_this.value[1] = 0; // value0
				}
				break;
			case 3: // 
				if (launch) { //  valuedefaultValue
					// 
					sendData = getSendData({ ...obj,
						column: 0
					}); // 
					const newData_1 = await getAsyncDataFc(sendData); // 
					_this.async_1 = [...newData_1]; // 
					_this.memoryData_1[value[0]] = [...newData_1]; // 
					_this.value[1] = value[1]; // 
					// 
					sendData = getSendData({ ...obj,
						datas: { ...datas,
							data_1: [...newData_1]
						},
						column: 1
					}); // 
					const newData_2 = await getAsyncDataFc(sendData);
					_this.async_2 = [...newData_2]; // 
					if (!_this.memoryData_2[value[0]])
						_this.memoryData_2[value[0]] = [];
					_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
					_this.value[2] = value[2]; // 
				} else { // picker
					let newData_1;
					let newData_2;
					switch (index) {
						case 0: // 
							// 
							if (!!_this.memoryData_1[value[0]]) { // 
								newData_1 = [..._this.memoryData_1[value[0]]]; // 
							} else {
								sendData = getSendData({ ...obj,
									column: 0
								}); // 
								newData_1 = await getAsyncDataFc(sendData);
								_this.memoryData_1[value[0]] = [...newData_1]; // 
							}
							_this.async_1 = [...newData_1]; // 
							_this.value[1] = 0; // 

							// 
							if (!_this.memoryData_2[value[0]])
								_this.memoryData_2[value[0]] = [];
							if (!!_this.memoryData_2[value[0]][value[1]]) { // 
								newData_2 = [..._this.memoryData_2[value[0]][value[1]]]; // 
							} else {
								sendData = getSendData({ ...obj,
									datas: { ...datas,
										data_1: [...newData_1]
									},
									column: 1
								}); // 
								newData_2 = await getAsyncDataFc(sendData);
								_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
							}
							_this.async_2 = [...newData_2] // 
							_this.value[2] = 0; // 
							break;
						case 1: // 
							log('')
							if (!_this.memoryData_2[value[0]])
								_this.memoryData_2[value[0]] = [];
							if (!!_this.memoryData_2[value[0]][value[1]]) { // 
								log('');
								newData_2 = [..._this.memoryData_2[value[0]][value[1]]]; // 
							} else {
								log('');
								sendData = getSendData(obj); // 
								newData_2 = await getAsyncDataFc(sendData);
								_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
							}
							_this.async_2 = [...newData_2]; // 
							_this.value[2] = 0; // 
							break;
						default:
							break;
					}
				}
				break;
		}
		_this.oldValue = [...this.value];
		_this.waiting = false;
	} catch (e) {
		//TODO handle the exception
		_this.oldValue = [...this.value];
		_this.waiting = false;

	}
}

const dateTime = 'picker-dateTime';
const date = 'picker-date';
const time = 'picker-time';
const pickerChoosedType = {
	pickerChoosedType_date: {
		name: 'date',
		value: 'p_date_choosed_'
	},
	pickerChoosedType_city: {
		name: 'city',
		value: 'p_city_choosed_'
	},
	pickerChoosedType_custom: {
		name: 'custom',
		value: 'p_custom_choosed_'
	},
	pickerChoosedType_custom2: {
		name: 'custom2',
		value: 'p_custom2_choosed_'
	},
	pickerChoosedType_provincialStreet: {
		name: 'provincialStreet',
		value: 'p_custom_provincialStreet_'
	}
};



const eventNames = {
	inputsChange: 'inputsChange',
	inputTap: 'inputTap'
};

const setValueType = {
	inputsObj: {
		name: 'inputsObj',
		itemName: ''
	},
	focusObj: {
		name: 'focusObj',
		itemName: ''
	}
};
const filterParamsArrayType = {
	setInputsValueFc: 'setInputsValueFc',
	setValue: 'setValue',
	picsUpLoadData: 'picsUpLoadData'
};

const _app = {
	log, //
	eventNames, // inputsemit
	UpLoadFile,
	pics_splice,
	sendSMS,
	pickerChoosedType,
	setValueType,
	filterParamsArrayType,
	verifyTypeObj, // 
	filterTypeObj, // 
	inputCustomTapFc,
	inputCustomTapCatchFc,
	getPickerAsyncData, // picker-custompicker-custom2
	showToast(msg) {
		uni.showToast({
			title: msg,
			icon: 'none'
		})
	},
	regTest(name, val) {
		return verifyTypeObj[name].reg.test(val);
	},
	isNumber(param) {
		return typeof(param) === 'number';
	},
	countDays(Y, M, val, mode) {
		let days = [];
		if (mode !== time) {
			days = new Date(Y, M + 1, 0).getDate();
			if (val) {
				val[2] = val[2] < days - 1 ? val[2] : days - 1;
			}
		}
		return {
			days,
			val
		};
	},
	countYears(sy, ey) {
		let _this = this;
		let y = [];
		let c = ey - sy;
		for (let i = 0; i <= c; i++) {
			y.push(sy + i);
		}
		return y;
	},
	hideLoading() {
		uni.hideLoading();
	},
	showLoading(msg, ifmask) {
		uni.showLoading({
			title: msg,
			mask: ifmask || false
		})
	},
	picker_date_obj: {
		dateTime,
		date,
		time
	},
	previewImage(imgPath, currentIndex) {
		if (!(imgPath instanceof Array))
			imgPath = [imgPath];
		uni.previewImage({
			urls: imgPath,
			current: currentIndex || 0
		})
	},
	checkbox_status(data) {	// 
		for (let i = 0; i < data.length; i++) {
			if (data[i] !== '') data[i] = true;
			else data[i] = false;
		}
		return data;
	},
	filterParams(params, type, checkOneVariableName) {
		if (params.length === 0)
			return {};
		const arr = getParamsArray(type);
		if (params.length > 1) {
			const o = {};
			Object.keys(params).forEach((item, index) => {
				o[arr[index]] = params[index];
			})
			return o;
		} else {
			if(params[0] instanceof Object){
				
				if(checkOneVariableName) {
					const ps = Object.keys(params[0]);
					for(let i = 0; i < ps.length; i++) {
						if(arr.includes(ps[i])) {
							return params[0];
						}
					}
					return { [arr[0]]: params[0] };
				}else{
					return params[0];
				}
			}else{
				return { [arr[0]]: params[0] };
			}
		}
	},
	formatNum(num) {
		return num<10?('0' + num):num;
	}
}
export default _app;

function getParamsArray(type) {
	let arr;
	switch (type) {
		case filterParamsArrayType.setInputsValueFc:
			arr = ['param', 'value', 'fail', 'isVariableName'];
			break;
		case filterParamsArrayType.setValue:
			arr = ['setDatas'];
			break;
		case filterParamsArrayType.picsUpLoadData:
			arr = ['setPicsDatas', 'scb', 'fcb'];
			break;
		default:
			arr = [];
			break;
	}
	return arr;
}


